
.cv-stats{

    li{
        background-color: rgba(255, 255, 255, 0.26);
        border-radius: 20px;
        padding: 6px 15px;
        display: inline-block;
        margin: 3px 0;
        color: #fff;
        font-weight: 600;
        font-size: .8em;
    }
}

.cv-specs{
    column-count: 2;
    font-size: .9em;
    margin: 1.5em 0 3em 0;
    line-height: 1.7;

    li{
        margin-bottom: 15px;
        display: inline-block;
        width: 100%;
        
        span{
            color: white;
            width: 100%;
            display: inline-block;
        }
    }
}