
%slice{
    width: 100%;
    position: relative;
    min-height: 50%;

    @include bp($screen-md){
        width: 50%;
        float: left;
        height: 100%;
    }
}
.right {
    @extend %slice;
}

.left{
    @extend %slice;
    background-color: $brand-primary;
    h1,h2,h3{
        color: white;
    }
}

@include bp($screen-md){
    .center-v{
        @include centralize-v;
    }
    .center{
        @include centralize;
    }
}

section {
    overflow: hidden;
    width: 100%;
    
    @include bp($screen-md){
        height: 100vh;
        min-height: 50vh;
    }
}

.section-space {
    padding: 5em;

    @include bp($screen-md, max){
        padding: 2em;
        text-align: center;
    }
}
