

.project-img-container {
    text-align: center;
    padding: 20px;
    z-index: 2;
    position: relative;

    img {
        display: inline-block;
        box-shadow: $shadow;
        max-width: 100%;
        height: auto;
        vertical-align: top;
        
        &:nth-child(n+2) {
            margin-top: -70px;
            max-width: 40%;
        }
        &:nth-child(2) {
            margin-right: 15px;
        }
    }
}


.projects{
    .left .center-v:after {
        content: "";
        @include centralize;
        width: 80%;
        top: 60%;
        height: 60px;
        background-image: url('../../img/white-dot.png');
        background-size: 20px;
        background-repeat: round;

        @include bp($screen-md, max){
            width: 90%;
            top: 70%;
        }
    }
}