

%photos{
    height: 50vmin;
    width: 70vmin;
    max-width: 50vw;
    background-size: cover;
    background-position: center;
    box-shadow: $shadow;

    @include bp($screen-md, max){
        display: none;
    }
    @media (max-height: 545px){
        display: none;
    }
}

#photo-1{
    @extend %photos;
    background-image: url('../../img/ggg.png');
    float: right;
    margin-right: -6%;
    
}
#photo-2{
    @extend %photos;
    background-image: url('../../img/rova.png');
    background-position: 0px center;
    margin-left: -6%;
}

.intro-title{
    @include bp($screen-md, max){
        @include centralize();
        text-align: center;
    }
    @include bp($screen-md){
        width: 70vmin;
        max-width: 50vw;
        float: right;
        margin-right: -6%;
        padding-right: 7%;
    }
}

nav{
    padding: 20px;
    margin-left: 4em;

    @include bp($screen-md, max){
        text-align: center;
        margin-left: 0;
    }

    a{
        text-decoration: none;
        letter-spacing: 8px;
        text-transform: uppercase;
        font-weight: 800;
        padding-bottom: 20px;
        display: inline-block;
    }
}

#intro{
    
    .left{
        @include bp($screen-md, max){
            height: 70vh;
        }

        .center-v{
            right: 0;
        }
    }
    .right{
        .center-v{
            left: 0;
        }
    }
}